<template>
	<div class="inteledi-changePassWord">
		<w-header title="" :border="true">
		</w-header>
			<div class="changepwd-content">
				<van-cell-group>
					<van-field
						v-model="formData.oldPwd"
						required
						clearable
						type="password"
						label=""
						placeholder=""
						:error-message="errorMessage.oldPwd"
					/>

					<van-field
						v-model="formData.newPwd"
						required
						clearable
						type="password"
						label=""
						:error-message="errorMessage.newPwd"
						placeholder=""
					/>

					<van-field
						v-model="formData.newPwdAffirm"
						required
						clearable
						type="password"
						label=""
						:error-message="errorMessage.newPwdAffirm"
						placeholder=""
					/>
				</van-cell-group>
			</div>
			<div class="changepwd-bottom">
				<van-button
					class="add-btn fx-no-radius"
					type="primary"
					@click="onClickConfirm"
				></van-button>
			</div>
	</div >
</template>

<script>
export default {
	name: 'changePassWord',
	data () {
		return {
			validateFlag: false,
			formData: {
				newPwd: '',
				oldPwd: '',
				newPwdAffirm: ''
			},
			errorMessage: {
				newPwd: '',
				oldPwd: '',
				newPwdAffirm: ''
			}
		}
	},
	methods: {
		onClickConfirm () {
			this.submitHanle()
		},
		validate () {
			return new Promise(resolve => {
				this.clearValidate()
				let validateFlag = true
				if (this.formData.oldPwd === '') {
					this.errorMessage.oldPwd = ''
					validateFlag = false
				}
				if (this.formData.oldPwd.length > 50) {
					this.errorMessage.oldPwd = '50'
					validateFlag = false
				}
				if (this.formData.newPwd === '') {
					this.errorMessage.newPwd = ''
					validateFlag = false
				}
				if (this.formData.newPwd === this.formData.oldPwd) {
					this.errorMessage.newPwd = ''
					validateFlag = false
				}
				if (this.formData.newPwd.length > 50) {
					this.errorMessage.newPwd = '50'
					validateFlag = false
				}
				if (this.formData.newPwdAffirm === '') {
					this.errorMessage.newPwdAffirm = ''
					validateFlag = false
				}
				if (this.formData.newPwdAffirm.length > 50) {
					this.errorMessage.newPwdAffirm = '50'
					validateFlag = false
				}
				if (this.formData.newPwdAffirm !== this.formData.newPwd) {
					this.errorMessage.newPwdAffirm = ''
					validateFlag = false
				}
				resolve(validateFlag)
			})
		},
		submitHanle () {
			this.validate().then(validateFlag => {
				validateFlag && this.PutPassword().then((res) => {
					this.$dialog.alert({
						title: '',
						message: res.message
					}).then(() => {
						this.$router.push('/main')
					})
				})
			})
		},
		PutPassword () {
			const params = {
				password: this.formData.oldPwd,
				newPassword: this.formData.newPwd
			}
			return this.$fxApi('common.changePassword')({ data: params })
		},
		clearValidate () {
			for (let key in this.errorMessage) {
				this.errorMessage[key] = ''
			}
		}
	}
}
</script>
<style lang="stylus" scoped>
@import '~$assets/stylus/varsty.styl'
.inteledi-changePassWord{
	height:100%
	background:$fxWhite
	display: flex
	flex-direction: column
	.changepwd-content {
			flex:1
	}
	.changepwd-bottom {
		width:100%
		display: flex
		flex-direction: row
		.add-btn {
			width:100%
		}
	}
}
</style>
